@charset "UTF-8";

.my_woopayjp_cc{
  position: relative;
  &--empty{
    display: none;
    & + .my_woopayjp_cc__alert{
      display: block;
    }
  }
  &__alert{
    display: none;
    color: #9b9b9b;
    font-weight: bold;
  }
  &__default{
    &--on{
      color: #82b181;
    }
    &--off{
      color: #dbdbdb;
    }
  }
}


.woopayjp-msg{
  padding: 1em;
  border: 1px solid #00a000;
  color: #00a000;
  background: #e0f5e2;
  margin: 2em 0;
  position: relative;
  p{
    margin: 0 2em 0 0;
  }
  a{
    position: absolute;
    top: 50%;
    right: 2em;
    margin-top: -0.5em;
    &:link, &:visited{
      color: #aaa;
      font-weight: bold;
      text-decoration: none;
    }
    &:hover, &:active{
      color: #444;
      font-weight:bold;
      text-decoration: none;
    }

  }
  &--error{
    border-color: #c90f01;
    color: #c90f01;
    background: #ffe6e7;
  }
}

// For storefront menu icon
.woocommerce-MyAccount-navigation-link.woocommerce-MyAccount-navigation-link--credit-cards a:before{
  content: "\f09d";
}
